header {
  position: fixed;
  width: 100%;
  padding: 5px 0;
  color: #fff;
  background: rgba(240, 47, 23, 1);
  background: -moz-linear-gradient(top, rgba(240, 47, 23, 1) 0%, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, 0) 100%);
  background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(240, 47, 23, 1)), color-stop(0%, rgba(0, 0, 0, 1)), color-stop(100%, rgba(0, 0, 0, 0)));
  background: -webkit-linear-gradient(top, rgba(240, 47, 23, 1) 0%, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, 0) 100%);
  background: -o-linear-gradient(top, rgba(240, 47, 23, 1) 0%, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, 0) 100%);
  background: -ms-linear-gradient(top, rgba(240, 47, 23, 1) 0%, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, 0) 100%);
  background: linear-gradient(to bottom, rgba(240, 47, 23, 1) 0%, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, 0) 100%);
  filter: progid:dximagetransform.microsoft.gradient(startColorstr="#f02f17", endColorstr="#000000", GradientType=0);
  z-index: 1000;
  top: 0;
  transition: background 0.1s ease-in-out;

  .brand {
    background-image: url(logo.svg);
    background-size: contain;
    height: 50px;
    background-repeat: no-repeat;
    padding-left: 60px;
    display: flex;
    align-items: center;
    color: #fff;
    font-size: 13px;
    text-decoration: none;
  }

  .container {
    display: flex;
    max-width: 1140px;
    padding-right: 15px;
    padding-left: 15px;
    margin: 0 auto;
  }

  .nav-primary {
    align-self: center;
    justify-content: flex-end;
    display: flex;
    flex: 1;

    ul {
      display: flex;
      flex-wrap: wrap;
      padding-left: 0;
      list-style: none;
      margin: 0;

      li {
        a {
          color: #fff;
          display: block;
          padding: 0.5rem 1rem;
          text-decoration: none;
          transition: color 0.1s ease-in-out;
        }

        &.toggle {
          display: flex;
          align-items: center;
          border-left: 1px solid;
          padding-left: 15px;
          margin-left: 5px;
          transition: border-color 0.1s ease-in-out;
          cursor: pointer;

          svg {
            fill: #fff;
            margin-top: 2px;
            transition: fill 0.1s ease-in-out;
          }
        }
      }
    }
  }
}

.main.light {
  header {
    background: #e6e6e6;

    .brand {
      color: #222;
      background-image: url(logo-dark.svg);
    }

    .nav-primary {
      ul {
        li {
          a {
            color: #222;
          }

          &.toggle {
            border-color: #222;

            svg {
              fill: #222;
            }
          }
        }
      }
    }
  }
}