.page-header {
  display: flex;
  align-items: flex-end;
  min-height: 250px;
  padding: 35px 0;
  background-color: rgba(62, 62, 62, 0.75);
  transition: background-color 0.1s ease-in-out;

  h1 {
    color: #fff;
    transition: color 0.1s ease-in-out;
  }
}

.main.light {
  .page-header {
    background-color: rgba(232, 232, 232, 0.75);

    h1 {
      color: #222;
    }
  }
}

.page-header + .container {
  margin-top: 35px;
}