#homepage-banner {
  height: 100vh;
  background-color: #4a4a4a;
  background-size: cover;
  background-position: center center;
  transition: background-color 0.1s ease-in-out;

  .container {
    width: 100%;
    max-width: 1140px;
    padding-right: 15px;
    padding-left: 15px;
    margin: 0 auto;
  }

  .inner {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    position: relative;
    z-index: 2;
    text-align: center;

    h1,
    h2 {
      color: #fff;
      margin-bottom: 25px;
      transition: color 0.1s ease-in-out;
    }

    h1 {
      margin-top: 0;
      font-size: 36px;
    }

    svg {
      color: #fff;
      margin: 0 7px;
    }
  }
}

.main.light {
  #homepage-banner {
    background-color: #fff;

    h1,
    h2,
    svg {
      color: #222;
    }
  }
}