footer {
  display: flex;
  padding: 6px 25px;
  background-color: rgba(62, 62, 62, 0.75);
  color: #ffffff;

  p {
    text-align: center;
    width: 100%;

    a {
      color: #fff;
      text-decoration: none;
    }
  }
}

.main.light {
  footer {
    background: #e6e6e6;

    p,
    a {
      color: #222;
    }
  }
}