.info-banner {
  .container {
    display: flex;
    min-height: 500px;

    > * {
      flex: 1 0 50%;
      max-width: 50%;
    }

    .image-container {
      position: relative;

      .image {
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        position: absolute;
        height: 100%;
        width: 50vw;
      }
    }

    .text-container {
      padding: 40px 40px 40px 0;
      box-sizing: border-box;
      color: #fff;
      align-self: center;

      p {
        font-size: 18px;
        line-height: 26px;

        a {
          color: #fff;
        }
      }
    }
  }

  &.image-left {
    .text-container {
      order: 2;
      padding: 40px 0 40px 40px;
    }

    .image-container {
      .image {
        right: 0;
      }
    }
  }
}

.main.light {
  .info-banner {
    .container {
      .text-container {
        color: #222;

        a {
          color: #222;
        }
      }
    }
  }
}