.container {
    width: 100%;
    max-width: 1140px;
    padding-right: 15px;
    padding-left: 15px;
    margin: 0 auto;
}

.post-container {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -12.5px 20px;

    .loader {
        border: 5px solid #f3f3f3;
        border-top: 5px solid #3498db;
        border-radius: 50%;
        width: 30px;
        height: 30px;
        animation: spin 2s linear infinite;
        margin: 0 auto 15px;
    }

    .project-box-wrapper {
        flex: 1 0 33.333%;
        max-width: 33.333%;
        padding: 0 12.5px;
        box-sizing: border-box;
    }

    .project-box {
        height: 280px;
        position: relative;
        display: block;
        background-position: top;
        background-repeat: no-repeat;
        background-size: 100%;
        transition: all .3s ease;
        margin-bottom: 25px;
        cursor: pointer;

        &:hover {
            background-size: 110%;
        }

        .year {
            position: absolute;
            top: 0;
            right: 0;
            background-color: rgba(0, 0, 0, 0.75);
            padding: 7px;
            color: #fff;
            font-size: 16px;
            transition: color 0.1s ease-in-out, background-color 0.1s ease-in-out;
        }

        h3 {
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            padding: 8px 15px;
            background-color: rgba(0, 0, 0, 0.75);
            margin-bottom: 0;
            font-size: 23px;
            color: #fff;
            text-decoration: none;
            transition: color 0.1s ease-in-out, background-color 0.1s ease-in-out;

            a {
                color: #fff;
                text-decoration: none;
            }
        }
    }
}

.further-info {
    position: fixed;
    top: 0;
    background-color: #d8d8d8;
    right: 0;
    padding: 30px;
    bottom: 0;
    width: 50%;
    z-index: 1000;
    border-left: 5px solid #848484;

    .text-holder {
        overflow-y: scroll;
        max-height: 100%;
    }

    h2 {
        margin: 0 0 25px;
    }

    p {
        white-space: pre-line;

        span {
            &::after {
                content: ', ';
            }

            &:last-of-type {
                &::after {
                    display: none;
                }
            }
        }
    }

    a.btn {
        background-color: #4a4a4a;
        padding: 10px 20px;
        border-radius: 10px;
        display: inline-block;
        color: #fff;
        text-decoration: none;
        margin: 17px 0 0;
        transition: all .25s ease-in-out;

        &:hover {
            background-color: #616161;
        }
    }

    .key-page-wrapper {
        display: flex;
        max-width: 100%;
        margin: 40px -10px;

        .key-page {
            flex: 1 0 calc(33.333% - 10px);
            max-width: calc(33.333% - 10px);
            height: 200px;
            background-position: top;
            background-repeat: no-repeat;
            background-size: 100%;
            position: relative;
            transition: all .3s ease;
            cursor: pointer;
            margin: 0 5px;

            &:hover {
                background-size: 110%;
            }

            p {
                position: absolute;
                bottom: 0;
                left: 0;
                right: 0;
                padding: 4px 10px;
                background-color: rgba(0, 0, 0, 0.75);
                margin-bottom: 0;
                font-size: 18px;
                color: #fff;
                text-decoration: none;

                a {
                    color: #fff;
                    text-decoration: none;
                }
            }
        }
    }

    .close {
        position: absolute;
        top: 0;
        right: 0;
        font-size: 42px;
        line-height: 10px;
        padding: 15px;
        width: 20px;
        text-align: center;
        height: 20px;
        background-color: #333;
        color: #fff;
        cursor: pointer;
    }
}

.further-info + .overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.75);
}

.main.light {
    .post-container .project-box .year,
    .post-container .project-box h3 {
        background-color: rgba(236, 236, 236, 0.75);
        color: #222;
    }
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}